@tailwind base;
@tailwind components;
@tailwind utilities;

.main-navigation {
  @apply hover:text-primary px-3 text-base font-medium h-16 border-b-4 border-white flex items-center;
}

.top-navigation .active {
  @apply text-primary border-primary font-bold;
}

.button-blue {
  @apply text-blue-800 bg-blue-200 hover:bg-blue-600 hover:text-white;
}

.bg-hero {
  background: #006de1; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #06affd,
    #0c79cc
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #06affd,
    #0c79cc
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.bg-hero:hover {
  background: #006de1; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #0c79cc,
    #06affd
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #0c79cc,
    #06affd
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.btn {
  @apply font-bold py-2 px-4 rounded;
}

.btn-blue {
  @apply bg-blue-500 text-white;
}
.btn-blue:hover {
  @apply bg-blue-700;
}

.input {
  @apply bg-gray-200 appearance-none  rounded w-full py-2 px-4 text-gray-700 leading-tight focus:bg-white;
}
.input-blue {
  @apply bg-gray-200 appearance-none  rounded w-full py-2 px-4 text-blue-600 leading-tight focus:bg-white;
}

.input-border {
  @apply border-2 border-gray-200 focus:outline-none focus:border-blue-500;
}

.input-white {
  @apply bg-white appearance-none rounded w-full py-2 px-4 text-gray-700 leading-tight focus:outline-none focus:bg-gray-100;
}

.input-gray-lg {
  @apply bg-white py-3 px-4 rounded leading-tight text-gray-800 focus:bg-white border border-gray-400 focus:border-blue-500;
}
.input-gray-lg-danger {
  @apply bg-white py-3 px-4 rounded leading-tight text-yellow-800 focus:bg-white border border-yellow-600 focus:outline-none;
}
.alert-success {
  @apply bg-green-100 border-l-4 border-green-500 rounded-b text-green-900 px-4 py-3 shadow-md;
}

.alert-danger {
  @apply bg-red-100 border-l-4 border-red-500 rounded-b text-red-900 px-4 py-3 shadow-md;
}

.alert-warning {
  @apply bg-yellow-100 border-l-4 border-yellow-500 rounded-b text-yellow-900 px-4 py-3 shadow-md;
}

.alert-info {
  @apply bg-blue-100 border-l-4 border-blue-500 rounded-b text-blue-900 px-4 py-3 shadow-md;
}

.alert-secondary {
  @apply bg-gray-200 border-l-4 border-gray-500 rounded-b text-gray-900 px-4 py-3 shadow-md;
}

/* Done by Emmy */
.question-level {
  @apply border-2 border-gray-300 px-3 py-1 rounded cursor-pointer  hover:border-green-500 hover:text-green-500 grid grid-cols-4 items-center;
}
.question-level-green {
  @apply border-2 border-green-300 px-3 py-1 rounded cursor-pointer grid grid-cols-4 items-center bg-green-50;
}

.dropzone {
  text-align: center;
  padding: 20px;
  border: 3px dashed #eeeeee;
  background-color: #fafafa;
  color: #bdbdbd;

  margin-bottom: 20px;
  @apply py-12;
}

.accept {
  border-color: #107c10 !important;
}

.reject {
  border-color: #d83b01 !important;
}
.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1050;
  display: block;
  overflow: hidden;
  -webkit-overflow-scrolling: touch;
  outline: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  /* background-color: rgb(30, 138, 132); */
}

.blue-gradient {
  @apply bg-gradient-to-b from-darkLightGreen  to-blue-600;
}
.dark-blue-gradient {
  @apply bg-gradient-to-b from-blue2  to-darkLightGreenItemTo;
}
.dark-blue-gradient-item1 {
  @apply bg-gradient-to-b from-darkLightGreenItemTo  to-darkLightGreenItemTo;
}
.dark-blue-gradient-item {
  background-color: #177de0;
  border: 2px solid #ffffff4d;
}
.tooltip {
  @apply invisible absolute;
}
.has-tooltip:hover .tooltip {
  @apply visible z-50;
}
.user-card-invigirator-video-container {
  @apply bg-blue-900  rounded-md relative overflow-hidden;
}

.user-card-invigirator-video-container video {
  @apply h-56 object-fill;
}

.user-card-invigirator-video-container .name-badge {
  @apply bg-blue-500 bg-opacity-70 shadow font-bold rounded-lg px-2 py-1 absolute bottom-2 ml-2 left-0 transition-all duration-200 ease-in-out;
}

.user-card-invigirator-video-container:hover .name-badge {
  @apply bottom-12;
}

.user-card-invigirator-video-container .controls-container {
  @apply px-0 py-1 absolute -bottom-20 left-2 mr-0 transition-all duration-300 ease-in-out;
}

.user-card-invigirator-video-container:hover .controls-container {
  @apply bottom-1;
}

.side-nav-container .active {
  @apply text-blue-700 border-r-4 -mr-1 bg-blue-200 font-bold border-blue-700;
}

.proctoring-evaluator-navigation {
  background-color: #1b1b39;
}

.proctoring-body-contents {
  background-color: #0d0d14;
}

.proctoring-side-section {
  background-color: #121226;
}

body,
* {
  box-sizing: border-box;
}

.side-nav-icons {
  @apply block py-1 px-3 hover:bg-blue-500 hover:text-white text-center border-gray-200;
}

.side-nav-icons .icon-item-nav {
  @apply text-4xl block mx-auto m-0 -mb-1;
}

.proctoring-action-button {
  @apply text-center bg-blue-600 rounded-md p-1 border-2 border-blue-500 hover:bg-blue-500 text-white  hover:text-white;
}
.text-editor {
  width: 1086px;
  margin: 0 auto;
}
.ql-editor {
  min-height: 100px;
}
.timer-count-down {
  font-family: "Chakra Petch", sans-serif;
}

.card-done-dark {
  background-color: #008242 !important;
}
.card-done-light {
  background-color: #009060 !important;
}
.card-stopped-dark {
  background-color: #ce0202 !important;
}
.card-stopped-light {
  background-color: #8e0000 !important;
}
.card-default-dark {
  background-color: #3f51b5 !important;
  border: 1px solid #fff;
}
.card-default-light {
  background-color: #121226 !important;
}

.hover-child {
  display: none;
}
.hover-parent:hover {
  position: relative;
}
.hover-parent:hover .hover-child {
  display: block;
  position: absolute;
  bottom: 2rem;
}

.z-lg {
  z-index: 11050 !important;
}

.z-xlg {
  z-index: 111050 !important;
}
